import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiEntrenaService } from 'src/app/Services/Api/ApiEntrena.service';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalController, IonSlides, AlertController } from '@ionic/angular';
import { DesignerModalPage } from '../../Modals/Designer/designer/designer.page';
import { DataComparnyService } from 'src/app/Services/dataCompany.service';
import { environments } from 'src/environments/environment';

@Component({
  selector: 'app-designer',
  templateUrl: './designer.page.html',
  styleUrls: ['./designer.page.scss'],
})
export class DesignerPage implements OnInit {

  JSONDesigner = {
    save: false,
    pages: [],
    count: 0,
    prom: 0,
    tot: 0,
    apro: 0
  };

 

  load = false;

  nameDesing = '';

  viewInit = '1';
  save = false;

  pagesCount = 0;
  pageActive = 1;

  color = '';

  route = environments.URL_RESORCES;

  @ViewChild('slide', {static: false}) slide: IonSlides;
  constructor(
    private apiEntrena: ApiEntrenaService,
    private toasCtrl: ToastService,
    private storage: StorageWebService,
    private ActiveRoute: ActivatedRoute,
    private modalCtrl: ModalController,
    private router: Router,
    private dataCompany: DataComparnyService,
    private alert: AlertController
    ) { 
    
      this.dataCompany.$color.subscribe((color) => {
          this.color = color;
      })

    }

  ngOnInit() {
  }

  ionViewWillEnter() { // se ejecuta antes de iniciar la vista
    this.loadDesign();
  }

  async loadDesign() { // carga la vista del formulario
    const login = await this.storage.getLogin();

    if ( login ) {
        this.apiEntrena.readDesigner(login[0].token, this.ActiveRoute.snapshot.paramMap.get('id')).subscribe((data: any) => {
          console.log(data, 'MI DATICA');
          if (data.status === 'ok') {
            this.JSONDesigner = data.response.JSONEntrena;
            this.nameDesing = data.response.name;
            this.pagesCount = this.JSONDesigner.pages.length;
            const pagesquiz = this.JSONDesigner.pages.filter((item) => item.type === 'quiz');

            pagesquiz.forEach((item: any) => {
              const children: any[] = item.Chilndren;
              children.forEach((child) => {
                if (child.type === 'radio' || child.type === 'checkbox') {
                  this.JSONDesigner.count = this.JSONDesigner.count + 1;
                }
              })
            })

            this.load = true;
          } else {
            this.toasCtrl.newCreatedToast('Could not read design, please try again later', false)
          }
        }, (error) => {
          this.toasCtrl.newCreatedToast('Could not read design, please try again later', false)
        })
    }
  }

  async deletePage(idx) {
    const alert = await this.alert.create({
      header: '¿Quieres eliminar esta página?',
      message: 'Se borrarán todos los campos de la página de forma pernamente',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel'
        },
        {
          text: 'Aceptar',
          handler: () => {
            this.JSONDesigner.pages.splice(idx, 1);

            const pagesquiz = this.JSONDesigner.pages.filter((item) => item.type === 'quiz');
        
            pagesquiz.forEach((item: any) => {
                      const children: any[] = item.Chilndren;
                      children.forEach((child) => {
                        if (child.type === 'radio' || child.type === 'checkbox') {
                          this.JSONDesigner.count = this.JSONDesigner.count + 1;
                        }
                      })
                    })
            this.autoSave();
            this.pagesCount = this.JSONDesigner.pages.length;
          }
        }
      ]
    })

    await alert.present();
  }



  async deleteChild(p, i) {
    const alert = await this.alert.create({
      header: '¿Quieres eliminar este campo?',
      message: 'Se borrará de forma pernamente',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel'
        },
        {
          text: 'Aceptar',
          handler: () => {
            this.JSONDesigner.pages[p].Chilndren.splice(i, 1);

            const pagesquiz = this.JSONDesigner.pages.filter((item) => item.type === 'quiz');
        
            pagesquiz.forEach((item: any) => {
                      const children: any[] = item.Chilndren;
                      children.forEach((child) => {
                        if (child.type === 'radio' || child.type === 'checkbox') {
                          this.JSONDesigner.count = this.JSONDesigner.count + 1;
                        }
                      })
                    })
            this.autoSave();
            this.pagesCount = this.JSONDesigner.pages.length;
          }
        }
      ]
    })

    await alert.present();
  }


  
  prev() {
    this.slide.slidePrev(300).then(() => {
      this.pageActive = this.pageActive - 1;
    })
  }

  next() {
    this.slide.slideNext(300).then(() => {
      this.pageActive = this.pageActive + 1;
    })
  }

  async autoSave() { // Guarda los cambios cada vez que se ejcuta alguna funcion que altera el formulario
    console.log(this.JSONDesigner);
    this.save = true;
    const login = await this.storage.getLogin();

    if ( login ) {
        // tslint:disable-next-line:max-line-length
        this.apiEntrena.updateDesigner(login[0].token, {_id: this.ActiveRoute.snapshot.paramMap.get('id'), name: this.nameDesing, JSONEntrena: this.JSONDesigner}).subscribe((data: any) => {
          console.log(data);
          if (data.status === 'ok') {
            this.save = false;
            this.JSONDesigner = data.response.JSONEntrena;
            this.nameDesing = data.response.name;
          } else {
            this.save = false;
            this.toasCtrl.newCreatedToast('Could not update design, please try again later', false)
          }
        }, (error) => {
          this.save = false;
          this.toasCtrl.newCreatedToast('Could not update design, please try again later', false)
        })
    }
  }

 

  // Designer Pages
  // Cambio de las paginas y formulario

  changeTitleDesign( event ) { // Cambia el titulo del formulario
    this.nameDesing = event.detail.value;
    
  }

  changePromDesign( event ) { // Cambia el titulo del formulario
    this.JSONDesigner.apro = parseFloat(event.detail.value);
    
  }
  newPages() { // inserta nuevas paginas al formulario
    const idPage = this.JSONDesigner.pages.length + 1;
    this.JSONDesigner.pages.push({
      id: idPage,
      name: '',
      type: '',
      Chilndren: []
    });
    this.autoSave();
    this.pagesCount = this.JSONDesigner.pages.length;
  }

  titlePage( event, id ) { // Cambia el titulo de la pagina
    this.JSONDesigner.pages = this.JSONDesigner.pages.map((item: any) => {
      if (item.id === id) {
        item.name = event.detail.value;
      }
      return item;
    })

    
  }

  titlesave() {
    this.autoSave();
  }

  changeTypePage( event, id) { // cambio el tipo de pagina
    this.JSONDesigner.pages = this.JSONDesigner.pages.map((item: any) => {
      if (item.id === id) {
        item.type = event.detail.value;
      }
      return item;
    })
    this.autoSave();
  }

  createInputPage( event, id) { //  crea los input de acuerdo a una pagina
    this.JSONDesigner.pages = this.JSONDesigner.pages.map((item: any) => {
      if (item.id === id) {
        const idChild = item.Chilndren.length + 1;
        let valuesSchema = {};

        if (event.detail.value === 'textline') {
          valuesSchema = {
            title: '',
            value: '',
            answer: '',
            requerid: false
          }
        } else if (event.detail.value === 'textarea') {
          valuesSchema = {
            title: '',
            value: '',
            answer: '',
            requerid: false
          }
        } else if (event.detail.value === 'radio' || event.detail.value === 'checkbox') {
          valuesSchema = {
            title: '',
            opts: [],
            answer: {
              sum: 0,
              value: 0,
              text: '', // solo aplica para radios
              op: [] //  solo aplica para checkbox
            },
            requerid: false
          }
        } else if (event.detail.value === 'image' || event.detail.value === 'file' || event.detail.value === 'video') {
          valuesSchema = {
            title: '',
            src: '',
            mime: '',
            name: ''
          }
        }
        item.Chilndren.push({
          id: idChild,
          type: event.detail.value,
          values: valuesSchema
        });
      }
      return item;
    })
    this.autoSave();
  }


  // Alterando los campos ya creados por medio del Modal

  async modalChangeInput(child, idpag) {

      console.log('Bien');
      const modal = await this.modalCtrl.create({
        component: DesignerModalPage,
        componentProps: {
          data: child,
          idpage: idpag
        },
        keyboardClose: true
      })

      await modal.present();

      const modalClose = await modal.onWillDismiss();

      if (modalClose) {

        if (modalClose.data) {

        if (modalClose.data.reload) {
          this.JSONDesigner.pages = this.JSONDesigner.pages.map((item: any) => {
            if (item.id === modalClose.data.info.page) {
              item.Chilndren.map((children: any) => {
                if (children.id === modalClose.data.info.values.id) {
                  children = modalClose.data.info.values;
                }
                return children;
              });
            }
            return item;
          })
          this.autoSave();
        }
      }
    }
  }

  // Reordenando los inputs 

  changeOrder(event, page) {

    console.log(event, page);

    this.JSONDesigner.pages = this.JSONDesigner.pages.map((item: any) => {
      if (item.id === page) {
        const itemChange = item.Chilndren.splice(event.detail.from, 1)[0];
        console.log(itemChange);
        item.Chilndren.splice(event.detail.to, 0, itemChange)
      }
      return item;
    });
    event.detail.complete();
    this.autoSave();

   
  }

  exit(){
    this.router.navigate(['/designer-list']);
  }
 

}
