import { Component, OnInit, Input, ElementRef, ViewChild } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { HttpErrorResponse, HttpEventType } from '@angular/common/http';
import { catchError, map } from 'rxjs/operators';
import { of } from 'rxjs';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { ApiEntrenaService } from 'src/app/Services/Api/ApiEntrena.service';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';
import { environments } from 'src/environments/environment';

@Component({
  selector: 'app-designer',
  templateUrl: './designer.page.html',
  styleUrls: ['./designer.page.scss'],
})
export class DesignerModalPage implements OnInit {
  @Input() data: any;
  @Input() idpage: any;

  titleInput = '';
  values: any = {};

  @ViewChild('fileUpload', {static: false}) fileUpload: ElementRef;
  files = [];
  filesRendering = [];

  route = environments.URL_RESORCES;

  
  constructor(
    private modalCtrl: ModalController,
    private storage: StorageWebService,
    private apiEntrena: ApiEntrenaService,
    private toasCtrl: ToastService
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
   this.titleInput = this.data.type;
   this.values = this.data.values;
   console.log(this.values);
  }

  addOpt() {
    this.values.opts.push({
      text: '',
      value: '',
      checked: false
    });
  }

  deleteOpt(i) {
    this.values.opts.splice(i, 1);
  }

  // FILES


  async uploadFile(file) {
    const Login = await this.storage.getLogin();

    if (Login) {
      const formData = new FormData();
      formData.append('file', file.data);
      file.inProgress = true;
      console.log(Login);
      this.apiEntrena.upload(formData, Login[0].token).pipe(
        map((event: any ) => {
          console.log('Holaa')
          switch (event.type) {
            case HttpEventType.UploadProgress:
              file.progress = Math.round(event.loaded * 1 / event.total);
              break;
            case HttpEventType.Response:
              return event;
          }
        }),
        catchError((error: HttpErrorResponse) => {
          file.inProgress = false;
          return of(`${file.data.name} upload failed.`);
        })).subscribe((event: any) => {
          if (typeof (event) === 'object') {
            console.log(event.body, ' Respuesta');

            if (event.body.status === 'ok') {
               this.values.src = event.body.response;
               this.values.mime = file.data.type;
               this.values.name = file.data.name;
               this.saveJson();
            } else {
              this.toasCtrl.newCreatedToast('Upload failed', false)
            }
          }
        });
    }
  }

  private uploadFiles() {
    this.fileUpload.nativeElement.value = '';
    this.files.forEach(file => {
      this.uploadFile(file);
    });
}

  onClick() {
    const fileUpload = this.fileUpload.nativeElement;
    fileUpload.onchange = () => {
    // tslint:disable-next-line:prefer-for-of
    for (let index = 0; index < fileUpload.files.length; index++) {
     const file: File = fileUpload.files[index];
     console.log(file, file.name)

     this.files.push({ data: file, inProgress: false, progress: 0});
    }
    
    };
    fileUpload.click();
}

deletedFile() {
  this.files = [];
}

  // CLOSE FILES

  save() {
    if (this.data.type === 'image' || this.data.type === 'video' || this.data.type === 'file' ) {
      this.uploadFiles();
    } else {
      this.saveJson();
    }
    
  }

  saveJson() {
    this.modalCtrl.dismiss({
      reload: true,
      info: {
        values: this.values,
        page: this.idpage
      }
    });
  }

  close() {
    this.modalCtrl.dismiss({});
  }

}
